import { Injectable } from "@angular/core";
import {
  IEmpAssignManagerDeleteRequest,
  IEmpAssignManagerModel,
  IEmpAssignManagerRequest,
} from "app/personnel-setup/models/emp-employment-info/assign-as-org-manager";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeAssignAsOrgManagerService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpAssignManagerRequest) {
    return this.post<IResponse<IEmpAssignManagerModel>>(
      "Personnel/Employees/Create_EmployeeManger",
      data
    );
  }

  getList(
    // employeeInstanceId: number,
    orgId: number,
    params: IPageParams
  ) {
    return this.get<IResponseList<IEmpAssignManagerModel[]>>(
      "Personnel/Employees/GetList_EmployeeManger",
      {
        params: {
          ...params,
          // prtid: employeeInstanceId,
          orgid: orgId,
        },
      }
    );
  }

  getAll(employeeInstanceId: number, orgId: number) {
    return this.get<IResponse<IEmpAssignManagerModel[]>>(
      "Personnel/Employees/GetAll_EmployeeManger",
      {
        params: {
          prtid: employeeInstanceId,
          orgid: orgId,
        },
      }
    );
  }

  Delete(params: IEmpAssignManagerDeleteRequest) {
    return this.delete<IResponse>("Personnel/Employees/Delete_EmployeeManger", {
      params: params,
    });
  }
}
