import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IPersonInstanceOrg } from "app/org-management/model/org-structure";
import { empAssignManagerDisplay } from "app/personnel-setup/mapper/emp-employment";
import {
  IEmpAssignManagerDeleteRequest,
  IEmpAssignManagerDisplayModel,
  IEmpAssignManagerModel,
  IEmpAssignManagerRequest,
} from "app/personnel-setup/models/emp-employment-info/assign-as-org-manager";
import { EmployeeAssignAsOrgManagerService } from "app/personnel-setup/services/employees/employement-information/employee-assign-as-org-manager.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-assign-as-org-manager-dialog",
  templateUrl: "./assign-as-org-manager-dialog.component.html",
  styleUrls: ["./assign-as-org-manager-dialog.component.scss"],
})
export class AssignAsOrgManagerDialogComponent
  extends AppHelpers
  implements OnInit
{
  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "employee",
    "startDate",
    "endDate",
    "actions",
  ];
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IEmpAssignManagerDisplayModel>;

  isLoaded: boolean = false;

  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private EmployeeAssignAsOrgManagerService: EmployeeAssignAsOrgManagerService,
    @Inject(MAT_DIALOG_DATA) public org: IPersonInstanceOrg,
    public dialogRef: MatDialogRef<AssignAsOrgManagerDialogComponent>
  ) {
    super();

    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.emp_job_info_assign_manager_sec
    );
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      from_Date: [null, [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);

    if (this.checkNoBusinessOrCompany() && this.org.person_Instance_ID > 0) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) {
      this.page.dialogSize = pageSize;
    }
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.dialogSize,
    };
    this.EmployeeAssignAsOrgManagerService.getList(
      // this.org.person_Instance_ID,
      this.org.core_Org?.id,
      params
    )
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IEmpAssignManagerModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            empAssignManagerDisplay(res.data)
          );
          this.page.length = res.meta.totalItemCount;
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IEmpAssignManagerRequest = {
        Person_Instance_ID: this.org.person_Instance_ID,
        Person_Instance_Org_ID: this.org.id,
        From_Date: HelpersFunctions.mapDateReverse(this.form.value.from_Date),
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmpAssignManagerRequest) {
    this.showLoading();
    this.EmployeeAssignAsOrgManagerService.create(model).subscribe(
      (res: IResponse<IEmpAssignManagerModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      }
    );
  }

  onDelete(manager: IEmpAssignManagerDisplayModel): void {
    let msg = `${manager.employee} (${manager.startDate} - ${manager.endDate})`;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(manager);
      }
    });
  }

  delete(manager: IEmpAssignManagerDisplayModel) {
    let deleteParams: IEmpAssignManagerDeleteRequest = {
      prtid: manager.person_Instance_ID,
      orgperid: manager.person_Instance_Org_ID,
      fromdate: HelpersFunctions.mapDateReverse(manager.from_Date),
    };
    this.showLoading();
    this.EmployeeAssignAsOrgManagerService.Delete(deleteParams).subscribe(
      (res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.dialogSize);
          } else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.dialogSize);
          }
          // this.isUpdated(true);
        } else {
          this.errorList(res.message.messages);
          this.hideLoading();
        }
      }
    );
  }

  clearForm() {
    this.form.reset(/* { id: 0 } */);
    this.submitted = false;
  }
}
