import { Component, Inject } from "@angular/core";
import { IEmployeeJobTitleModel } from "app/org-management/model/job-title";
import { ICloseEmpStatusModel } from "app/personnel-setup/models/employee-employment-info";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import {
  EmployeeJobInformationService
} from "app/personnel-setup/services/employees/employement-information/employee-job-information.service";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { FormControl, Validators } from "@angular/forms";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

@Component({
  selector: "app-close-job-dialog",
  templateUrl: "./close-job-dialog.component.html",
  styleUrls: ["./close-job-dialog.component.scss"]
})
export class CloseJobDialogComponent extends AppHelpers {
  CloseDate = new FormControl("", Validators.required);
  submitted: boolean = false;

  constructor(private employeeJobInfoService: EmployeeJobInformationService,
              private dialogRef: MatDialogRef<CloseJobDialogComponent>,
              @Inject(MAT_DIALOG_DATA) public job: IEmployeeJobTitleModel) {
    super();
  }


  submit() {
    this.submitted = true;
    if (this.CloseDate.valid) {
      this.showLoading();
      let params: ICloseEmpStatusModel = {
        Employee_Job_Title_ID: this.job?.id,
        CloseDate: HelpersFunctions.transformData(this.CloseDate.value)
      };
      this.employeeJobInfoService
        .close(params)
        .subscribe((res: IResponse) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.success(res?.message?.messageTypeName);
            this.CloseDate.reset();
            this.submitted = false;
            this.dialogRef.close();
          } else {
            this.errorList(res.message.messages);
          }
          this.hideLoading();
        });
    }
  }

}
